@import url(https://fonts.googleapis.com/css2?family=PT+Serif&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0 auto;text-align:center;box-sizing:inherit;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}.header{padding-top:10px;overflow:visible;display:unset}.header .logo{font-family:'PT Serif';font-weight:bold}.header .menu{background-color:#1971c2;color:white;width:100%;margin:0;padding:10px 0 10px 0;position:-webkit-sticky;position:sticky;top:0}.header .menu div{display:inline-block;margin:10px 80px 10px 80px;cursor:pointer;font-family:'PT Serif'}.header .menu div:hover{font-weight:bold;text-decoration:underline}.header img{border-radius:100%}.table{text-align:left;display:block;border-bottom:3px solid}.about{margin:70px 20px 0px 20px;padding:0 40px 0 40px}.about div{display:flex}.about li{list-style:none;padding-left:20px}.about .introduce{text-align:left;line-height:1.75em;width:50%}.about .career{text-align:left;line-height:1.75em;width:50%}.about .date{display:inline;padding-left:10px;padding-right:5px;font-weight:bold}.skill{margin:70px 20px 0px 20px;padding:0 40px 0 40px}.skill .skill_contents{display:flex;height:auto}.skill .skill_contents p{align-self:center;margin:auto}.skill .skill_list{width:100%;height:auto;align-items:center}.skill .skill_list:hover{box-shadow:6px 8px 20px lightgray}.skill .skill_list:hover p:hover{font-weight:bold}.skill .skill_name{width:90%;height:auto;margin:10px;padding:10px 0px;box-shadow:6px 8px 20px lightgray;margin-bottom:20%}.skill img{width:40px;height:auto;margin:10px}.skill .mid_img{width:55px;height:auto}.project{margin:70px 20px 0px 20px;padding:0 40px 0 40px}.project .project_contents{width:100%;display:flex;flex-wrap:wrap}.project .project_contents .pj_img{max-width:40%}.project .pj_img{box-sizing:border-box;background-size:contain;background-repeat:no-repeat;background-position:center;margin:20px 50px 30px 50px;width:50%;height:17em;flex:30% 1}.project .pj_img :hover{cursor:pointer}.project .pj_img .pj_des{background-color:rgba(255,255,255,0.5);box-sizing:border-box;vertical-align:middle;width:auto;height:inherit}.project .pj_img .pj_des:hover{background-color:rgba(255,255,255,0.3);box-shadow:6px 8px 16px lightgray}.project .pj_img .pj_des h2{color:#1971c2;margin:0 auto;padding:35px}.contact{margin:70px 20px 0px 20px;padding:0 40px 0 40px}.contact .info{float:left;padding:30px;text-align:left}.contact .form{padding:30px;margin-bottom:20px;float:right;width:50%;background-color:#e7f5ff}.contact .form input,.contact .form textarea{margin-bottom:10px;width:90%;padding:12px;border:none;border-radius:5px;resize:none}textarea{height:100px}.contact .form button{width:60%;padding:12px;border:solid 2px;border-radius:5px;background-color:#1971c2;color:white}

.modal{position:fixed;top:0;right:0;bottom:0;left:0;max-width:100% !important;background:rgba(0,0,0,0.2);overflow-y:visible !important;overflow-y:initial !important}.hidden{display:none}.modal_content{width:600px;height:auto;background:white;position:relative;padding:10px;margin:70px auto;border-radius:15px;text-align:center;box-sizing:border-box}.modal_content .modal_img{display:flex;justify-content:center;padding:1rem;position:relative;align-items:center;text-align:center;box-sizing:border-box}.modal_content .modal_content-2{max-height:400px;overflow-y:auto;padding:0 20px 0 20px}.modal_content img{width:500px;box-sizing:border-box;margin:0 10px 0 10px}.modal_content .pj_link::before{content:"✍️"}

